import React, { useState } from "react";
import classes from "./SignInPage.module.scss";
import "../../typography.scss";
import formClasses from "./Form.module.scss";

const initialState = {
  email: "",
  password: "",
  errors: {
    email: "",
    password: "",
  },
};

function SignInPage() {
  const [formData, setFormData] = useState(initialState);

  const handleEvent = (name, value) => {
    let errors = { ...formData.errors };
    switch (name) {
      case "email":
        errors.email = /^[a-zA-Z0-9]+@(?:[a-zA-Z0-9]+\.)+[A-Za-z]+$/g.test(
          value
        )
          ? ""
          : "Email is not valid!";
        break;
      case "password":
        errors.password =
          value.length < 8 ? "Password must be 8 characters long!" : "";
        break;
      default:
        break;
    }
    if (!value) {
      errors[name] = "";
    }
    setFormData({
      errors,
      [name]: value,
    });
  };
  return (
    <div className={classes.SignInPage}>
      {/* <img
        className={classes.SignInPage__bg}
        src="https://assets.nflxext.com/ffe/siteui/vlv3/757ab38f-5d08-40bc-b3eb-eaba63ed8203/c2e1c04b-d3eb-485c-baed-e9e9e0498474/IN-en-20210125-popsignuptwoweeks-perspective_alpha_website_small.jpg"
        srcset="
    https://assets.nflxext.com/ffe/siteui/vlv3/757ab38f-5d08-40bc-b3eb-eaba63ed8203/c2e1c04b-d3eb-485c-baed-e9e9e0498474/IN-en-20210125-popsignuptwoweeks-perspective_alpha_website_small.jpg  1000w,
    https://assets.nflxext.com/ffe/siteui/vlv3/757ab38f-5d08-40bc-b3eb-eaba63ed8203/c2e1c04b-d3eb-485c-baed-e9e9e0498474/IN-en-20210125-popsignuptwoweeks-perspective_alpha_website_medium.jpg 1500w,
    https://assets.nflxext.com/ffe/siteui/vlv3/757ab38f-5d08-40bc-b3eb-eaba63ed8203/c2e1c04b-d3eb-485c-baed-e9e9e0498474/IN-en-20210125-popsignuptwoweeks-perspective_alpha_website_large.jpg  1800w
  "
        alt="bg_image"
      /> */}
      <div className={classes.container}>
        <div className={formClasses.Form}>
          <h1 className={"heading-primary mg-btm-medium"}>Sign In</h1>
          <div className={formClasses.Form__group}>
            <input
              type="email"
              name="email"
              id="email"
              className={formClasses.Form__input}
              placeholder="Email or Phone Number"
              onChange={(e) => handleEvent(e.target.name, e.target.value)}
            />
            <label for="email" className={formClasses.Form__label}>
              Email or Phone Number
            </label>
            <span className={formClasses.Form__validationInfo}>
              {formData.errors.email && formData.errors.email}
            </span>
          </div>

          <div className={formClasses.Form__group}>
            <input
              type="password"
              name="password"
              id="password"
              className={formClasses.Form__input}
              placeholder="Password"
              onChange={(e) => handleEvent(e.target.name, e.target.value)}
            />
            <label for="password" className={formClasses.Form__label}>
              Password
            </label>
            <span className={formClasses.Form__validationInfo}>
              {formData.errors.email && formData.errors.password}
            </span>
          </div>
          {/* <div className={formClasses.Form__group}> */}
          <button className={`btn btn__primary btn--red`}>Sign In</button>
          {/* </div> */}
          <div className={formClasses.Form__help}>
            <div className={formClasses.Form__groupHelp}>
              <input
                type="checkbox"
                name="rememberme"
                id="rememberme"
                className={`${formClasses.Form__helpLink} ${formClasses.Form__checkbox}`}
              />
              <label for="rememberme">Remember Me ?</label>
            </div>
            <a href="?#" className={formClasses.Form__helpLink}>
              Need Help?
            </a>
          </div>
          <div className={formClasses.Form__helperText}>
            Or Sign in using below social links
          </div>
          <div className={formClasses.Form__socialLinks}>
            <ul className={formClasses.socialLink__list}>
              <li className={formClasses.socialLink__item}>
                <a href="#" className="fa fa-facebook"></a>
              </li>
              <li className={formClasses.socialLink__item}>
                <a href="#" className="fa fa-google"></a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SignInPage;
